import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { FlattypeService } from './flattype.service';

@Component({
  selector: 'app-flattype',
  //moduleId: module.id,
  templateUrl: './flattype.component.html',
  styleUrls: ['./flattype.component.css']
})
export class FlattypeComponent implements OnInit {

  constructor(private fb: FormBuilder, private flattypeservice: FlattypeService) { }

  getFlatTypeList: any;
  getFlatType() {
    this.flattypeservice.getFlatTypeDetails().subscribe(response => {
      this.getFlatTypeList = response.flat_types_list;
    });

  }

  get_create_status;
  createFlattypeForm = this.fb.group({
    flat_type: ['']
  })

   
  createFlatypes() {
    this.flattypeservice.createFlatTypess(this.createFlattypeForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Flat Type')
        this.getFlatType();

      }
     
    })
  };

  get_delete_status;
  delete_json;
  deleteFlatType(flattypeObj) {
    this.delete_json = {
      'flat_types_list':flattypeObj
    }
    this.flattypeservice.deleteFlatTypes(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getFlatType();
      }
     
    })
  };


  ngOnInit() {
    this.getFlatType();
  }

}
