import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { RtuService } from './rtu.service';
import { BasestationService } from '../basestation/basestation.service';
import { BasestationchannelService } from '../basestationchannel/basestationchannel.service';
import { FlatService } from '../flat/flat.service';
import { InletService } from '../inlet/inlet.service';

@Component({
  selector: 'app-rtu',
  templateUrl: './rtu.component.html',
  styleUrls: ['./rtu.component.css']
})
export class RTUComponent implements OnInit {

  constructor(private fb: FormBuilder, private rtuservice: RtuService, private basestationservice: BasestationService, private basestationchannel: BasestationchannelService, private flatservice: FlatService, private inletservice: InletService) { }

  getRtuList;
  getRTUdata() {
    this.rtuservice.getRtuDetails().subscribe(response => {
      this.getRtuList = response.response;
    });
  }

  getallFlatdetails;
  getallFlatData() {
    this.flatservice.getallFlatDetailss().subscribe(response => {
      this.getallFlatdetails = response.flat_details;
    });

  }


  getInletList;
  getInletData() {
    this.inletservice.getInletDetails().subscribe(response => {
      this.getInletList = response.inlet_details;
    });

  }

  getBasestationChannelList;
  getBaseStationChannel() {
    this.basestationchannel.getBaseStationChannelDetails().subscribe(response => {
      this.getBasestationChannelList = response.base_station_channel_list;
    });

  }

  getBasestationList: any;
  getBaseStation() {
    this.basestationservice.getBaseStationDetails().subscribe(response => {
      this.getBasestationList = response.base_station_list;
    });

  }

  get_create_status;
  createRtuForm = this.fb.group({
    rtu_id: [''],
    base_station_id: [''],
    base_station_channel_id: [''],
    tag_name: [''],
    flat_id: [''],
    inlet_id: ['']
  })



  createRTU() {
    this.rtuservice.createRTUs(this.createRtuForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if (response.status == 200) {
        alert('Sucessfully Added Pipes')
        this.getRTUdata();
      }
    })
  }

  editResponse;
  editRTUFunction(getObj){
    let json_obj:any ={
      'rtu_id':getObj.rtu_id,
      'base_station_id':getObj.base_station._id,
      'base_station_channel_id':getObj.base_station_channel._id,
      'tag_name':getObj.tag_name,
      'flat_id':getObj.flat._id,
      'inlet_id':getObj.inlet._id,
      '_id':getObj._id
    };
    this.rtuservice.rtuEdit(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getRTUdata();
   }
    });
  }




  ngOnInit() {
    this.getRTUdata();
    this.getBaseStation();
    this.getBaseStationChannel();
    this.getallFlatData();
    this.getInletData();
  }
}
