import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import { BuildingService } from '../metadata/building/building.service';
import { ConsumptionService } from './consumption.service';
import { BuildingReportService } from '../reports/buildingreport/building-report.service';
import Drilldown from 'highcharts/modules/drilldown';
Drilldown(Highcharts);

@Component({
    selector: 'app-consumption-result',
    templateUrl: './consumption-result.component.html',
    styleUrls: ['./consumption-result.component.css']
})
export class ConsumptionResultComponent implements OnInit {

    constructor(private buildingService: BuildingService, private consumptionService: ConsumptionService, private buildingReportService: BuildingReportService) { }
    loader1;
    selectedYear = '2020';
    get_building_list;
   
    getBuildingLIst() {
        this.buildingService.getBuildingDetails().subscribe(res => {
            this.get_building_list = res.building_details;
            this.selectedOption = this.get_building_list[0]._id;
            this.get_block_building_report( this.selectedOption,this.selectedYear,this.selectedMonth);

        });
    };



    block_flat_response;
    block_flat_response_for_table;
    series_data;
    bar_drilldown;
    selectedOption:any;
    selectedMonth:any='';
    get_block_building_report(selectedOption, selectedYear, selectedMonth) {
        this.loader1 = true;
        const json_obj = {
            'building_id': selectedOption,
            'year':  selectedYear,
            'month': selectedMonth
        };
        
        if (json_obj.month) {
            this.consumptionService.getBuildingconsumptionMonthlysDetails(json_obj).subscribe(res => {
                this.block_flat_response = res.response;
                // if(block_flat_response.response.length !=0){
                    this.chart_render_function();
                // }
               
            });

        
            this.getBuildingConsumptionTable(json_obj);

        }
        else {
            this.consumptionService.getBuildingconsumptionYearlysDetails(json_obj).subscribe(res => {
                this.block_flat_response = res.response;
                // if(block_flat_response.response.length !=0){
                    this.chart_render_function();
                
            });
            this.getBuildingConsumptionTable(json_obj);
          
        }

    };

    getBuildingConsumptionTable(json_obj){
        this.buildingReportService.getBuildingReportForCurrentMonth(json_obj).subscribe(res => {
            this.block_flat_response_for_table = res.response;
            this.loader1 = false;
        });
    }

    data_list = [];
    chart_render_function(){
        this.data_list = [];
        var block_flat_response = this.block_flat_response;
        this.block_flat_response.forEach(value => {
            var data_iterate = {
                'name': value.block,
                'y': value.value,
                // drilldown: value.block
            };

            
            this.data_list.push(data_iterate);
           
        })

        if(this.data_list.length !=0){
            this.drilldownbar();
        }
     
    }


    drilldownDatasets=[];
   get_drilldown_flat(options){
      let drilldownData = options;
    this.drilldownDatasets=[];
      this.block_flat_response.forEach(value=>{
          if(drilldownData.name == value.block){
            
            let forTableJson = {
                'building_id': this.selectedOption,
                'block_id':value.block_id,
                'year':  this.selectedYear,
                'month': this.selectedMonth
            };
            this.getBuildingConsumptionTable(forTableJson);

            value.drilldown.forEach(val1=>{
                var series_json = {
                    'name': val1.flat,
                    'y': val1.value
                     } 
                 this.drilldownDatasets.push(series_json)   
            })
          }
       
    })

    if(this.drilldownDatasets.length !=0){
        this.data_list = [];
        this.data_list = this.drilldownDatasets;
        this.drilldownbar();
    }

    }



    getBlockId(building_details){
        let json_obj = {
            'block_id':building_details,
            'year':  this.selectedYear,
            'month': this.selectedMonth
        };

            this.buildingReportService.getBuildingReportForSearchDate(json_obj).subscribe(res => {
                this.block_flat_response_for_table = res.response;
                this.loader1 = false;
            });
        
       
    }
    


    back_to_block(){
        this.chart_render_function();
        let forTableJson = {
            'building_id': this.selectedOption,
            'year':  this.selectedYear,
            'month': this.selectedMonth
        };
        this.getBuildingConsumptionTable(forTableJson);

    }



    highcharts;
    chartOptions;
    drilldownbar() {
       
        this.highcharts = Highcharts;
        this.chartOptions = {
            chart: {
                type: 'column'
            },
            title: {
                text: 'Total Consumption'
            },
            subtitle: {
                text: 'Cosumption Per Block Detail Report'
            },
            accessibility: {
                announceNewData: {
                    enabled: true
                }
            },
            colors: ['#074e6f', '#017AEB', '#3DC4EB', '#51EBC8', '#43EB9F', '#EBB854', '#EBD454'],
            xAxis: {
                type: 'category'
            },
            yAxis: {
                title: {
                    text: 'Total Consumption by each Block'
                }

            },

            legend: {
                enabled: false
            },
            plotOptions: {
                series: {
                    borderWidth: 0,
                    dataLabels: {
                        enabled: true,
                        format: '{point.y}'
                    },
                    cursor: 'pointer',
                    point: {
                        events: {
                            click: (e)=> {
                                 
                                   this.get_drilldown_flat(e.point.options)
                            }
                        }
                    }
                },

            },

            tooltip: {
                headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
                pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}%</b> of total<br/>'
            },


            series: [
                {
                    name: "Block Details",
                    colorByPoint: true,
                    data:this.data_list
                }
            ],
         
          
        };
    }



    ngOnInit() {
        this.getBuildingLIst();
       
        

    }

}
