import { Component, OnInit } from '@angular/core';
import {rolematrixService} from './rolematrix.service';


@Component({
  selector: 'app-rolematrix',
  templateUrl: './rolematrix.component.html',
  styleUrls: ['./rolematrix.component.css']
})
export class RolematrixComponent implements OnInit {

  constructor(private rolematrix:rolematrixService) { }

  getRoleLists;
  getRoleList(){
    this.rolematrix.getUserRoleList().subscribe(res=>{
     this.getRoleLists = res.roles_list;
    });
  }


  ngOnInit() {
    this.getRoleList();
  }

}
