import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { globalToken } from '../globaltoken';

@Injectable({
  providedIn: 'root'
})
export class SignupService {
  constructor(private http: HttpClient) {}
   

  urls = new globalToken().globalUrl;
  httpOptions = {
      headers: new HttpHeaders({ 
          'Content-Type': 'application/x-www-form-urlencoded'

   })
    };

  
  signupintoserver(createObj):Observable<any> {
      let json_list= {
        'email':createObj.email,
        'password':createObj.password
      };
      return this.http.post(this.urls.urlLink+'/create_user',json_list )
         
  }
}
