import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import {InletService} from './inlet.service';
import { FlatService } from '../flat/flat.service';
import { InlettypeService } from '../inlettype/inlettype.service';

@Component({
  selector: 'app-inlet',
  templateUrl: './inlet.component.html',
  styleUrls: ['./inlet.component.css']
})
export class InletComponent implements OnInit {
  constructor(private fb: FormBuilder, private inletservice: InletService, private flatservice: FlatService, private inlettypeservice: InlettypeService) { }

  getInletList;
  getInletData(){
    this.inletservice.getInletDetails().subscribe(response => {
     this.getInletList = response.inlet_details;
 }); 
    
   }

   getInlettypedetails;
   getInletTypedata() {
     this.inlettypeservice.getInletTypess().subscribe(response => {
       this.getInlettypedetails = response.inlet_type_list;
     });
 
   }


   getallFlatdetails;
  getallFlatData() {
    this.flatservice.getallFlatDetailss().subscribe(response => {
      this.getallFlatdetails = response.flat_details;
    });

  }

   get_create_status;
   createInletform = this.fb.group({
    inlet_id: [''],
    flat_id: [''],
    inlet_name: [''],
    inlet_type: ['']
   })

   createInlets() {
    console.log(this.createInletform.value)
    this.inletservice.createInletss(this.createInletform.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Inlet')
        this.getInletData();

      }
     
    })
  }

  editResponse;
  editInletFunction(getInletObj){
    let json_obj:any ={
      'inlet_id':getInletObj.inlet_id,
      'flat_id':getInletObj.flat._id,
      'inlet_name':getInletObj.inlet_name,
      'inlet_type':getInletObj.inlet_type,
      '_id':getInletObj._id
    };
    this.inletservice.editInlet(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getInletData();
   }
    });
  }

  ngOnInit() {
    this.getInletData();
    this.getallFlatData();
    this.getInletTypedata();
  }

}
