import { Component, OnInit } from '@angular/core';
import { RtuService } from  '../../metadata/rtu/rtu.service';
import { RtoReportService } from './rto-report.service';
import * as Highcharts from 'highcharts';
import Bullet from 'highcharts/modules/bullet';
Bullet(Highcharts);

@Component({
  selector: 'app-rtoreport',
  //moduleId: module.id,
  templateUrl: './rtoreport.component.html',
  styleUrls: ['./rtoreport.component.css']
})
export class RtoreportComponent implements OnInit {

  constructor(private rtuService:RtuService, private rtoReportService:RtoReportService) { }

   getRtuList;
   get_flat_chart_report;

   years:string[] = ["2019","2020","2021","2022","2023","2024","2025"];
  selectedYear:any = "2020";

   monthList:any[] = [
    {'monthname':'Jan', 'monthnumber':1},
    {'monthname':'Feb', 'monthnumber':2},
    {'monthname':'Mar', 'monthnumber':3},
    {'monthname':'Apr', 'monthnumber':4},
    {'monthname':'May', 'monthnumber':5},
    {'monthname':'Jun', 'monthnumber':6},
    {'monthname':'Jul', 'monthnumber':7},
    {'monthname':'Aug', 'monthnumber':8},
    {'monthname':'Sep', 'monthnumber':9},
    {'monthname':'Oct', 'monthnumber':11},
    {'monthname':'Nov', 'monthnumber':12},
  ];


  getRTUdata() {
    this.rtuService.getRtuDetails().subscribe(response => {
      this.getRtuList = response.response;
    });
  }

  flat_ids;
  selectedOption:any;
  get_rtuReport(selectedOption){
   this.flat_ids = selectedOption;
  }

  get_rtureport_for_current_month() {
   let sendParam = {
      'rtu_id':this.flat_ids
   };

  //  month view chart report
  this.rtoReportService.getRTUCosumptionChartForMonth(sendParam).subscribe(response => {
     this.get_flat_chart_report = response.response;
     this.bulletCharts(this.get_flat_chart_report);
   });

 }


 get_rtureport_for_current_day() {
   let sendParam = {
      'rtu_id':this.flat_ids
   };

  //  day view chart
  this.rtoReportService.getRTUCosumptionChartForDay(sendParam).subscribe(response => {
     this.get_flat_chart_report = response.response;
     this.bulletCharts(this.get_flat_chart_report);
   })
 }

 get_rtureport_for_current_year() {
   let sendParam = {
      'rtu_id':this.flat_ids
   };

  //  year view chart
   this.rtoReportService.getRTUCosumptionChartForYear(sendParam).subscribe(response => {
     this.get_flat_chart_report = response.response;
     this.bulletCharts(this.get_flat_chart_report);
   })
 }


 selectedView:any;
 get_rtu_report(){
   let param = this.selectedView;
   switch(param){
      case 'year':
         this.get_rtureport_for_current_year();
      break;
      case 'month':
      this.get_rtureport_for_current_month();
      break;
      case 'day':
         this.get_rtureport_for_current_day();
      break;
   }
 }



  highcharts;
  chartOptions;
  bulletCharts(bulletdata) {
    this.highcharts = Highcharts;
    this.chartOptions = {   
      chart: {
        marginTop: 60,
         inverted: true,
        marginLeft: 60,
        type: 'bullet'
    },
      legend: {
        enabled: false
    },
    title: {
        text: '2020 January'
    },
    xAxis: {
        categories: ['Usage']
    },
     exporting: {
        enabled: false
    },
    yAxis: {
     gridLineWidth: 0,
        plotBands: [{
            from: 0,
            to: 150,
            color: '#91ddff'
        }, {
            from: 150,
            to: 225,
            color: '#91ddff'
        }, {
            from: 225,
            to: 9e9,
            color: '#91ddff'
        }],
        title: null
    },
     plotOptions: {
        series: {
            pointPadding: 0.25,
            borderWidth: 0,
            color: '#00344c',
            targetOptions: {
                width: '200%'
            }
        }
    },
    series: [{
        data: [{
            y: bulletdata.value,
            target: 210
        }]
    }],
    tooltip: {
        pointFormat: '<b>{point.y}</b> (with target at {point.target})'
    }
    };
       
  }




  ngOnInit() {
   this.getRTUdata();
  }

}
