import {Injectable} from '@angular/core';
import {Observable, throwError} from 'rxjs';
import {HttpClient,HttpHeaders} from '@angular/common/http';
import { globalToken } from '../globaltoken';

@Injectable({providedIn:'root'})

export class rolematrixService {

    constructor(private http:HttpClient){}

    urls = new globalToken().globalUrl;
    httpOptions = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json',
           'Authorization': this.urls.token
        })
    }


    getUserRoleList():Observable<any> {
        return this.http.get(this.urls.urlLink+'/get_all_roles', this.httpOptions)
    }

}
