import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ConsumerService } from './consumer.service';
import {FlatService} from '../flat/flat.service';

@Component({
  selector: 'app-consumer',
  templateUrl: './consumer.component.html',
  styleUrls: ['./consumer.component.css']
})
export class ConsumerComponent implements OnInit {


  constructor(private fb: FormBuilder, private consumerservice: ConsumerService,  private flatservice: FlatService) { }

  getConsumerData;
  getConsumerDetails() {
    this.consumerservice.getConsumerDetaliss().subscribe(response => {
      this.getConsumerData = response.consumer_details;
    });

  }

  getallFlatdetails;
  getallFlatData() {
    this.flatservice.getallFlatDetailss().subscribe(response => {
      this.getallFlatdetails = response.flat_details;
    });

  }

  get_create_status;
  createConsumerForm = this.fb.group({
    consumer_id: [''],
    first_name: [''],
    last_name: [''],
    email_id: [''],
    phone:[''],
    mobile:[''],
    flat_id:[''],
  })


  
  createConsumers(customerData) {
    // console.log(this.createConsumerForm.value)
    this.consumerservice.createConsumers(customerData).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added')
        this.getConsumerDetails();
      }
     
    })
  };

  get_delete_status;
  delete_json;
  deleteConsumer(deleteConsumerObj) {
    this.delete_json = {
      '_id':deleteConsumerObj._id
    }
    this.consumerservice.deleteconumerServices(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getConsumerDetails();
      }
     
    })
  }

  editResponse;
  editConsumerFunction(consumerObj){
    let json_obj:any ={
      'consumer_id':consumerObj.consumer_id,
      'first_name':consumerObj.first_name,
      'last_name':consumerObj.last_name,
      'email_id':consumerObj.email_id,
      'phone':consumerObj.phone,
      'mobile':consumerObj.mobile,
      'flat_id':consumerObj.flat_id,
      '_id':consumerObj._id
    };
    this.consumerservice.editConsumer(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getConsumerDetails();
   }
    });
  }

  ngOnInit() {
    this.getConsumerDetails();
    this.getallFlatData();
  }

}
