import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { globalToken } from '../../globaltoken';

@Injectable({
  providedIn: 'root'
})
export class RolebyusersService {
  constructor(private http: HttpClient) { }

  urls = new globalToken().globalUrl;
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': this.urls.token
    })
  };

  getRoleUsersList(): Observable<any> {
    return this.http.get(this.urls.urlLink+'/get_role_by_user', this.httpOptions)
  }

  assignRoletoUsersListApi(assignedRole): Observable<any> {
    return this.http.post(this.urls.urlLink+'/assign_role', assignedRole, this.httpOptions)
  }
}
