import { Component, OnInit } from '@angular/core';
import {RolebyusersService} from './rolebyusers.service';
import { FormBuilder } from '@angular/forms';
import {BuildingService} from '../../metadata/building/building.service';
import {rolematrixService} from '../../rolematrix/rolematrix.service';
import {UsersService} from '../users/users.service';

@Component({
  selector: 'app-rolebyusers',
  templateUrl: './rolebyusers.component.html',
  styleUrls: ['./rolebyusers.component.css']
})
export class RolebyusersComponent implements OnInit {

  constructor( private fb: FormBuilder, private rolebyuserServi:RolebyusersService, private buildingServi:BuildingService,private rolematrixServi:rolematrixService, private UsersServi:UsersService) { }


  rolebyuserList: any;
  getRolebyUsersListFunction() {
    this.rolebyuserServi.getRoleUsersList().subscribe(response => {
      this.rolebyuserList = response.users_list;
    });

  };


  buildingList;
  getBuildingListFunction(){
    this.buildingServi.getBuildingDetails().subscribe(res=>{
      this.buildingList = res.building_details;
    });
  };

  roleList;
  getRoleListFunction(){
    this.rolematrixServi.getUserRoleList().subscribe(res=>{
      this.roleList = res.roles_list;
    });
  };

  userList;
  getUserListFunction(){
    this.UsersServi.getUsersList().subscribe(res=>{
      this.userList = res.users_list;
    });
  }



  get_create_status;
  assignRoletoUserForm = this.fb.group({
    user_id: [''],
    role: [''],
    building_id: ['']
  })


    
  assignRoleToUser() {
    this.rolebyuserServi.assignRoletoUsersListApi(this.assignRoletoUserForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
      this.getRolebyUsersListFunction();

      }
     
    })
  }



  ngOnInit() {
    this.getRolebyUsersListFunction();
    this.getBuildingListFunction();
    this.getRoleListFunction();
    this.getUserListFunction();
  }

}
