import { Component, OnInit } from '@angular/core';
import { BlockReportService } from './block-report.service';
import { BlockService } from '../../metadata/block/block.service'
import * as Highcharts from 'highcharts';
import { FormBuilder } from '@angular/forms';
// import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-blockreport',
  //moduleId: module.id,
  templateUrl: './blockreport.component.html',
  styleUrls: ['./blockreport.component.css']
})
export class BlockreportComponent implements OnInit {

  constructor(private blockReportService:BlockReportService, private blockService:BlockService, private fb: FormBuilder) { }

  get_block_table_report;
  get_block_chart_report
  hideshow_selectmonth = false;


  years:string[] = ["2019","2020","2021","2022","2023","2024","2025"];
 

  monthList:any[] = [
   {'monthname':'Jan', 'monthnumber':1},
   {'monthname':'Feb', 'monthnumber':2},
   {'monthname':'Mar', 'monthnumber':3},
   {'monthname':'Apr', 'monthnumber':4},
   {'monthname':'May', 'monthnumber':5},
   {'monthname':'Jun', 'monthnumber':6},
   {'monthname':'Jul', 'monthnumber':7},
   {'monthname':'Aug', 'monthnumber':8},
   {'monthname':'Sep', 'monthnumber':9},
   {'monthname':'Oct', 'monthnumber':11},
   {'monthname':'Nov', 'monthnumber':12},
 ];

  get_block_list;
  getBlockServiceDetails() {
    this.blockService.getBlockServiceDetails().subscribe(response => {
      this.get_block_list = response.block_details;
      this.selectedOption = response.block_details[0]._id;
      if(response.status == '200'){
         this.get_blockreport_for_current_month();
      }
    })
  };

  block_ids;
  selectedOption:any;
  get_blockReport(selectedOption){
   this.block_ids = selectedOption;
  };

  
  
  get_blockreport_for_current_month() {
     this.hideshow_selectmonth = false;
    let sendParam = {
       'block_id':(!this.block_ids?this.selectedOption:this.block_ids),
       'year':(!this.selected_year?this.selectedYear:Number(this.selected_year))
    };

   //  month view table report
    this.blockReportService.getBlockReportForCurrentMonth(sendParam).subscribe(response => {
      this.get_block_table_report = response.response;
    });

   //  month view chart report
   this.blockReportService.getBlockCosumptionChartForMonth(sendParam).subscribe(response => {
      this.get_block_chart_report = response.response;
      if(this.get_block_chart_report){
         this.linechart();
      }
    });

  };

  
  get_blockreport_for_current_day() {
     this.hideshow_selectmonth = true;
    let sendParam = {
      'block_id':(!this.block_ids?this.selectedOption:this.block_ids),
       'month':(!this.selected_month?1:Number(this.selected_month)),
       'year':(!this.selected_year?this.selectedYear:Number(this.selected_year))
    };

   //  day view table
    this.blockReportService.getBlockReportForCurrentDay(sendParam).subscribe(response => {
      this.get_block_table_report = response.response;
    })

   //  day view chart
   this.blockReportService.getBlockCosumptionChartForDay(sendParam).subscribe(response => {
      this.get_block_chart_report = response.response;
      if(this.get_block_chart_report){
         this.linechart();
      }
    })
  };


  get_blockreport_for_current_year() {
     this.hideshow_selectmonth = false;
    let sendParam = {
      'block_id':(!this.block_ids?this.selectedOption:this.block_ids)
    };
   //  year view table
    this.blockReportService.getBlockReportForCurrentYear(sendParam).subscribe(response => {
      this.get_block_table_report = response.response;
    })

   //  year view chart
    this.blockReportService.getBlockCosumptionChartForYear(sendParam).subscribe(response => {
      this.get_block_chart_report = response.response;
      if(this.get_block_chart_report){
         this.linechart();
      }
    })
  };

 
  get_blockreport_for_search_date() {
    let sendParam = {
      'block_id':(!this.block_ids?this.selectedOption:this.block_ids)
    };


    this.blockReportService.getBlockReportForSearchDate(sendParam).subscribe(response => {
      this.get_block_table_report = response.response;
      if(this.get_block_chart_report){
         this.linechart();
      }
    })
  };

  selected_year;
  selectedYear:any = '2020';
  get_year(selectedYear){
     this.selected_year = selectedYear;
  }
 
  selected_month;
  get_month(selectedMonth){
   this.selected_month = selectedMonth;
  }


  selectedView:any="month";
  get_block_report(){
   let param = this.selectedView;
   switch(param){
      case 'year':
         this.get_blockreport_for_current_year();
      break;
      case 'month':
      this.get_blockreport_for_current_month();
      break;
      case 'day':
         this.get_blockreport_for_current_day();
      break;
   }
  }

  highcharts;
  chartOptions;
  linechart(){
   this.highcharts = Highcharts;
  this.chartOptions = {   
     chart: {
        type: 'areaspline'
     },
     title: {
        text: "Monthly Average Usage"
     },
     subtitle: {
        text: "Detail Report"
     },
     xAxis:{
        categories:this.get_block_chart_report.header_list
     },
     yAxis: {          
        title:{
           text:"Monthly Report"
        } 
     },
     legend: {
        layout: 'vertical',
        align: 'left',
        verticalAlign: 'top',
        x: 150,
        y: 100,
        floating: true,
        borderWidth: 1,
        backgroundColor:
            Highcharts.defaultOptions.legend.backgroundColor || '#FFFFFF'
    },
    tooltip: {
     shared: true,
     valueSuffix: ' units'
 },
 plotOptions: {
  areaspline: {
   dataLabels: {
      enabled: true
  },
      fillOpacity: 0.5
  }
},
     series: [
        {
           name: 'Water Usage',
           data: this.get_block_chart_report.value_list
        }
       
     ]
  };
 }


  ngOnInit() {
   
   this.getBlockServiceDetails();
  }


}
