import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { BasestationService } from '../basestation/basestation.service';
import { BasestationchannelService } from './basestationchannel.service';


@Component({
  selector: 'app-basestationchannel',
  templateUrl: './basestationchannel.component.html',
  styleUrls: ['./basestationchannel.component.css']
})
export class BasestationchannelComponent implements OnInit {

  constructor(private basestationchannel: BasestationchannelService, private fb: FormBuilder, private basestationService: BasestationService) { }

  getBasestationList: any;
  getBaseStation() {
    this.basestationService.getBaseStationDetails().subscribe(response => {
      this.getBasestationList = response.base_station_list;
    });

  }

  getBasestationChannelList;
  getBaseStationChannel() {
    this.basestationchannel.getBaseStationChannelDetails().subscribe(response => {
      this.getBasestationChannelList = response.base_station_channel_list;
    });

  }


  get_create_status;
  createBasestationChannelForm = this.fb.group({
    base_station_channel_id: [''],
    base_station_id: ['']
  })


  
  createBasestationChannels() {
    console.log(this.createBasestationChannelForm.value)
    this.basestationchannel.createBaseStationChannel(this.createBasestationChannelForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added')
        this.getBaseStationChannel();

      }
     
    })
  };

  get_delete_status;
  delete_json;
  deleteBasestationChannel(getBasestationChannelObj) {
    this.delete_json = {
      '_id':getBasestationChannelObj._id
    }
    this.basestationchannel.deleteBaseStationChannel(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getBaseStationChannel();
      }
     
    })
  }
  
  editResponse;
  editEachBaesstationChannelFunction(getBasestationChannelObj){
    let json_obj:any ={
      'base_station_channel_id':getBasestationChannelObj.base_station_channel_id,
      'base_station_id':getBasestationChannelObj.base_station_id,
      '_id':getBasestationChannelObj._id
    };
    this.basestationchannel.editBasestationChannel(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getBaseStationChannel();
   }
    });
  }


  ngOnInit() {
    this.getBaseStation();
    this.getBaseStationChannel();
  }

}
