import { Component, OnInit } from '@angular/core';
import {AuthorizepageaccessService} from '../authorizepageaccess.service';
import { Subscriber } from 'rxjs';
@Component({
  selector: 'app-left-sidebar',
  //moduleId: module.id,
  templateUrl: './left-sidebar.component.html',
  styleUrls: ['./left-sidebar.component.css']
})
export class LeftSidebarComponent implements OnInit {

  constructor(private authAccessScreen:AuthorizepageaccessService) { }

  closeNav() {
    document.getElementById("mySidenav").style.width = "0";
  }

  accessPageList:any;
  authAccessScreenLockFunction(){
    this.authAccessScreen.getAuthrizationListForpageLock().subscribe(res=>{
      this.accessPageList= res;
    });
  }
  
  ngOnInit() {
    this.authAccessScreenLockFunction();
  }

}
