import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { BlockService } from './block.service';
import {BuildingService} from '../building/building.service';

@Component({
  selector: 'app-block',
  templateUrl: './block.component.html',
  styleUrls: ['./block.component.css']
})
export class BlockComponent implements OnInit {

  constructor(private blockservice: BlockService, private fb: FormBuilder, private buildingService: BuildingService ) { }

  
  getBlockServiceDet;
  getBlockServiceDetailss() {
    this.blockservice.getBlockServiceDetails().subscribe(response => {
      this.getBlockServiceDet = response.block_details;
    });

  }

  getBuildingDet;
  getBuildingDetailss(){
    this.buildingService.getBuildingDetails().subscribe(response => {
     this.getBuildingDet = response.building_details;
 }); 
}

  get_create_status;
  createBlockForm = this.fb.group({
    block_id: [''],
    building_id: [''],
    block_name: [''],
    num_of_flats: [''],
  })


  
  createBlocks() {
    console.log(this.createBlockForm.value)
    this.blockservice.createBlockService(this.createBlockForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added')
        this.getBlockServiceDetailss();

      }
     
    })
  };


  get_delete_status;
  delete_json;
  deleteBlocks(getBlockdetailObj) {
    this.delete_json = {
      '_id':getBlockdetailObj._id
    }
    this.blockservice.deleteBlock(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response == 'success'){
        this.getBlockServiceDetailss();
      }
     
    })
  }

  editResponse;
  editBlockFunction(getBlockdetailObj){
    let json_obj:any ={
      'block_id':getBlockdetailObj.block_id,
      'building_id':getBlockdetailObj.building_id,
      'block_name':getBlockdetailObj.block_name,
      'num_of_flats':getBlockdetailObj.num_of_flats,
      '_id':getBlockdetailObj._id
    };
    this.blockservice.editBlock(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getBlockServiceDetailss();
   }
    });
  }


  ngOnInit() {
    this.getBlockServiceDetailss();
    this.getBuildingDetailss();
  }

}
