import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
// import { SweetAlertService } from 'angular-sweetalert-service';
import { BasestationService } from './basestation.service';
import {BlockService} from '../block/block.service';
import { BasestationtypeService } from '../basestationtype/basestationtype.service';



@Component({
  selector: 'app-basestation',
  templateUrl: './basestation.component.html',
  styleUrls: ['./basestation.component.css']
})
export class BasestationComponent implements OnInit {

  constructor( private fb: FormBuilder, private basestationservice: BasestationService, private blockservice:BlockService, private basestationtypeservice:BasestationtypeService) { }

  getBasestationList: any;
  getBaseStation() {
    this.basestationservice.getBaseStationDetails().subscribe(response => {
      this.getBasestationList = response.base_station_list;
    });

  }

  getBasestationTypeList;
  getBaseStationType() {
    this.basestationtypeservice.getBaseStationTypeDetails().subscribe(response => {
      this.getBasestationTypeList = response.base_station_types;
    });
  }
  

  getBlockServiceDet;
  getBlockServiceDetailss(){
    this.blockservice.getBlockServiceDetails().subscribe(response => {
     this.getBlockServiceDet = response.block_details;
 }); 
}

  get_create_status;
  createBasestationForm = this.fb.group({
    base_station_id: [''],
    block_id: [''],
    mac_id: [''],
    ip_address: [''],
    base_station_type:['']
  })


  
  createBasestation() {
   
    this.basestationservice.createBaseStation(this.createBasestationForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        this.getBaseStation();

      }
     
    })
  }


  get_delete_status;
  delete_json;
  deleteBasestation(baseStationObj) {
    this.delete_json = {
      '_id':baseStationObj
    }
    this.basestationservice.deleteBaseStation(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getBaseStation();
      }
     
    })
  }

  editResponse;
  editEachBaesstationFunction(baseStationObj){
    let json_obj:any ={
      'base_station_id':baseStationObj.base_station_id,
      'block_id':baseStationObj.block._id,
      'mac_id':baseStationObj.mac_id,
      'ip_address':baseStationObj.ip_address,
      'base_station_type':baseStationObj.base_station_type,
      '_id':baseStationObj._id
    };
    this.basestationservice.editEachBasestation(json_obj).subscribe(res=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getBaseStation();
   }
    });
  }

  ngOnInit() {
    this.getBaseStation();
    this.getBlockServiceDetailss();
    this.getBaseStationType();
    
  }

}
