import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { InlettypeService } from './inlettype.service';

@Component({
  selector: 'app-inlettype',
  //moduleId: module.id,
  templateUrl: './inlettype.component.html',
  styleUrls: ['./inlettype.component.css']
})
export class InlettypeComponent implements OnInit {
  constructor( private fb: FormBuilder, private inlettypeservice: InlettypeService) { }

  getInlettypedetails;
  getInletTypedata() {
    this.inlettypeservice.getInletTypess().subscribe(response => {
      this.getInlettypedetails = response.inlet_type_list;
    });

  }


  createInletTypeForm = this.fb.group({
    inlet_type: ['']
  })

  get_create_status;
  createInletTypes() {
    this.inlettypeservice.createInletTypess(this.createInletTypeForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Inline Type')
        this.getInletTypedata();

      }
     
    })
  }

  ngOnInit() {
    this.getInletTypedata();
  }

}
