import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { PipetypeService } from './pipetype.service';


@Component({
  selector: 'app-pipetype',
  //moduleId: module.id,
  templateUrl: './pipetype.component.html',
  styleUrls: ['./pipetype.component.css']
})
export class PipetypeComponent implements OnInit {

  constructor(private fb: FormBuilder, private pipetypeservice: PipetypeService) { }

  getPipeData;
  getPipeDetailsList() {
    this.pipetypeservice.getPipeDetailss().subscribe(response => {
      this.getPipeData = response.pipe_type_list;
    });
  }

  get_create_status;
  createPipetypeForm = this.fb.group({
    pipe_type: ['']
  })

  createPipetypes() {
    this.pipetypeservice.createPipeTypess(this.createPipetypeForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Pipes')
        this.getPipeDetailsList();

      }
     
    })
  }

  ngOnInit() {
    this.getPipeDetailsList();
  }

}
