import { Component, OnInit } from '@angular/core';
import { LoginService } from './login.service';
import { FormBuilder } from '@angular/forms';
import {  Router } from '@angular/router';
import {globalToken} from '../globaltoken';

// import { FormBuilder } from '@angular/forms';
import * as $ from 'jquery';
import { from } from 'rxjs';

@Component({
  selector: 'app-login',
  //moduleId: module.id,
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  constructor(private fb: FormBuilder, private loginServ: LoginService,public router: Router, private globalSev:globalToken) { }
  login_token;
  token_no;
  loginForm = this.fb.group({
    email:[''],
    password:['']
  })

  loader;
  wrong_password;
  login(){
  this.loader = true;
   this.loginServ.logintoserver(this.loginForm.value.email, this.loginForm.value.password).subscribe((response:any) => {
    this.login_token = response;
    this.token_no = response.token;
    this.loader = false;
    if(this.login_token.token != 'Error Token'){ 
     if(this.login_token.token != 'Incorrect Password'){
      this.getTokenFunction()
      this.router.navigate(['/home']);
    }
    else {
      this.wrong_password = true;
      setTimeout(()=>{
        this.wrong_password = false;
      },3000)
    }
  }
    else {
      this.wrong_password = true;
      setTimeout(()=>{
        this.wrong_password = false;
      },3000)
    }
   
}); 
  };


  getTokenFunction(){
    this.globalSev.getGlobalTokenFunction('Token'+' '+this.token_no);
  }

  ngOnInit() {
  }

}
