import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { BasestationtypeService } from './basestationtype.service';

@Component({
  selector: 'app-basestationtype',
  templateUrl: './basestationtype.component.html',
  styleUrls: ['./basestationtype.component.css']
})
export class BasestationtypeComponent implements OnInit {

  constructor(private basestationtypeservice: BasestationtypeService, private fb: FormBuilder) { }

  getBasestationTypeList;
  getBaseStationType() {
    this.basestationtypeservice.getBaseStationTypeDetails().subscribe(response => {
      this.getBasestationTypeList = response.base_station_types;
    });
  }

  get_create_status;
  createBasestationTypeForm = this.fb.group({
    base_station_type: ['']
  })


  
  createBasestationTypes() {
    console.log(this.createBasestationTypeForm.value)
    this.basestationtypeservice.createBaseStationTypeService(this.createBasestationTypeForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Base Station')
        this.getBaseStationType();

      }
     
    })
  };

  get_delete_status;
  delete_json;
  deleteBasestationType(basestationTypeObj) {
    this.delete_json = {
      'base_station_type':basestationTypeObj
    }
    this.basestationtypeservice.deleteBaseStationtype(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getBaseStationType();
      }
     
    })
  };


  editResponse;
  editBasestationtypeFunction(basestationTypeObj){
    let json_obj:any ={
      'base_station_channel_id':basestationTypeObj.base_station_channel_id,
      'base_station_id':basestationTypeObj.base_station_id,
      '_id':basestationTypeObj._id
    };
    this.basestationtypeservice.editBasestationtype(json_obj).subscribe((res:any)=>{
      this.editResponse = res.response;
    })
  }


  ngOnInit() {
    this.getBaseStationType();
  }

}
