import { Component, OnInit } from '@angular/core';
import {ConsumerService} from '../../metadata/consumer/consumer.service';
import {ConsumerReportService} from '../consumer-report/consumer-report.service';

import {FlatReportService} from '../flatreport/flat-report.service';
import {FlatService} from '../../metadata/flat/flat.service';
import * as Highcharts from 'highcharts';
import Bullet from 'highcharts/modules/bullet';
Bullet(Highcharts);

@Component({
  selector: 'app-consumer-report',
  templateUrl: './consumer-report.component.html',
  styleUrls: ['./consumer-report.component.css']
})
export class ConsumerReportComponent implements OnInit {

  constructor(private consumerService:ConsumerService, private consumerreportService:ConsumerReportService, private flatReportService:FlatReportService, private flatService:FlatService) { }


  get_block_table_report;
  get_flat_chart_report;
  loader1 = true;
  selectedOption:any;
  bullet_chart_data;
  
  get_consumer_list;
  getConsumerlists() {
    this.consumerService.getConsumerDetaliss().subscribe(response => {
      this.get_consumer_list = response.consumer_details;
     
    })
  };

  get_flat_list;
  getFlatServiceDetails() {
    this.flatService.getallFlatDetailss().subscribe(response => {
      this.get_flat_list = response.flat_details;
      if(response.status == '200'){
         this.loader1 = false;
         this.selectedOption = response.flat_details[0]._id;
         this.flat_id = response.flat_details[0]._id;
         this.get_consumer_report_go();
      }
    
    })
  }

  selectedView:any = 'month';

  years:string[] = ["2019","2020","2021","2022","2023","2024","2025"];
  selectedYear = "2020";

   monthList:any[] = [
    {'monthname':'Jan', 'monthnumber':1},
    {'monthname':'Feb', 'monthnumber':2},
    {'monthname':'Mar', 'monthnumber':3},
    {'monthname':'Apr', 'monthnumber':4},
    {'monthname':'May', 'monthnumber':5},
    {'monthname':'Jun', 'monthnumber':6},
    {'monthname':'Jul', 'monthnumber':7},
    {'monthname':'Aug', 'monthnumber':8},
    {'monthname':'Sep', 'monthnumber':9},
    {'monthname':'Oct', 'monthnumber':11},
    {'monthname':'Nov', 'monthnumber':12},
  ];

  
  flat_id;
  get_ConsumerReport(consumerDet){
  this.flat_id = consumerDet;
  this.loader1 = true;
  this.get_consumer_report_for_current_year();
  }

  selected_day;
  get_consumer_chart_report;
  get_consumer_table_report

// per month
 get_consumer_report_for_current_month() {
    this.selected_day = false;
    let sendParam = {
       'flat_id':this.flat_id,
       'year':(!this.selected_year?this.selectedYear:Number(this.selected_year))
    };
 
    this.consumerreportService.getCosumptionChartForMonth(sendParam).subscribe(response => {
    this.get_consumer_chart_report = response.response;
    this.bullet_chart_data = response.status;
    this.loader1 = false;
    if(response.response){
       this.bulletCharts(this.get_consumer_chart_report);
       this.loader1 = false;
      }
    });

      //  month view chart report
  this.flatReportService.getFlatCosumptionChartForMonth(sendParam).subscribe(response => {
    this.get_flat_chart_report = response.response;
    this.flatRportStatus = response.status;
    if(response.response){
     this.linechart();
    }
  });

  
  };
  // per month



  // per year
  get_consumer_report_for_current_year() {
    this.selected_day = false;
    let sendParam = {
       'flat_id':this.flat_id
    };

    this.consumerreportService.getCosumptionChartForYear(sendParam).subscribe(response => {
      this.get_consumer_chart_report = response.response;
      this.bullet_chart_data = response.status;
      this.loader1 = false;
      if(response.response){
     this.bulletCharts(this.get_consumer_chart_report);
    }
    });

      //  year view chart report
  this.flatReportService.getFlatCosumptionChartForYear(sendParam).subscribe(response => {
    this.get_flat_chart_report = response.response;
    this.flatRportStatus = response.status;
    if(response.response){
     this.linechart();
    }
  });

  };
  // per year

  // per day
  flatRportStatus;
  get_consumer_report_for_current_day() {
    this.selected_day = true;
    let sendParam = {
       'flat_id':this.flat_id,
       'month':(!this.selected_month?1:Number(this.selected_month)),
       'year':(!this.selected_year?this.selectedYear: Number(this.selected_year))
    };

   //  day view chart
   this.consumerreportService.getCosumptionChartForDay(sendParam).subscribe(response => {
      this.get_consumer_chart_report = response.response;
      this.bullet_chart_data = response.status;
      this.loader1 = false;
      if(response.response){
       this.bulletCharts(this.get_consumer_chart_report);
     }
    });

 //  day view chart
  this.flatReportService.getFlatCosumptionChartForDay(sendParam).subscribe(response => {
    this.get_flat_chart_report = response.response;
    this.flatRportStatus = response.status;
    if(response.response){
     this.linechart();
    }
  });

  };
  // per day


  selected_year;
  
  get_year(selectedYear){
     this.selected_year = selectedYear;
     this.get_consumer_report_for_current_year();
  }

  selected_month;
  get_month(selectedMonth){
   this.selected_month = selectedMonth;
   this.get_consumer_report_for_current_day();
 
  }

  get_consumer_report_go(){
    this.loader1 = true;
   if(this.selectedView == 'year'){
     this.get_consumer_report_for_current_year();
   } else if(this.selectedView == 'month'){
     this.get_consumer_report_for_current_month();
   }
   else if(this.selectedView == 'day'){
     this.get_consumer_report_for_current_day();
   }
 };


  highcharts;
  chartOptions;
  bulletCharts(bulletData) {
    this.highcharts = Highcharts;
    this.chartOptions = {   
      chart: {
        marginTop: 60,
         inverted: true,
        marginLeft: 60,
        type: 'bullet'
    },
      legend: {
        enabled: false
    },
    title: {
        text: 'Current Consumption ' +(!this.selected_year?'':this.selected_year)
    },
    xAxis: {
        categories: ['Usage']
    },
     exporting: {
        enabled: false
    },
    yAxis: {
      gridLineWidth: 0,
         plotBands: [{
             from: 0,
             to: (!bulletData?30000:(bulletData.target==0)?30000:(bulletData.target + 80000)),
             color: '#91ddff'
         }],
         title: null
     },
     plotOptions: {
      series: {
          pointPadding: 0.25,
          borderWidth: 0,
          color: (!bulletData?'#8BC34A': (bulletData.percentage==0)?'#8BC34A': (bulletData.percentage < 70)?'#8BC34A':((bulletData.percentage > 70) && (bulletData.percentage < 90))?'#FF2AA0':'#FF2A1F'),
          targetOptions: {
              width: '200%'
          }
      }
  },
  series: [{
    data: [{
        y: (!bulletData?0:(bulletData.value==0)?100 : (bulletData.value)),
        target: (!bulletData?25000:(bulletData.target==0)?25000:bulletData.target)
    }]
}],
    tooltip: {
        pointFormat: '<b>{point.y}</b> (with target at {point.target})'
    }
    };
       
  }




  highcharts1;
  chartOptions1;

  linechart(){
    this.highcharts1 = Highcharts;
   this.chartOptions1 = {   
      chart: {
         type: 'areaspline'
      },
      title: {
         text: "Monthly Average Usage"
      },
      subtitle: {
         text: "Detail Report"
      },
      xAxis:{
         categories:this.get_flat_chart_report.header_list
      },
      yAxis: {          
         title:{
            text:"Monthly Report"
         } 
      },
      legend: {
         layout: 'vertical',
         align: 'left',
         verticalAlign: 'top',
         x: 150,
         y: 100,
         floating: true,
         borderWidth: 1,
         backgroundColor:
             Highcharts.defaultOptions.legend.backgroundColor || '#FFFFFF'
     },
     tooltip: {
      shared: true,
      valueSuffix: ' units'
  },
  plotOptions: {
   areaspline: {
    dataLabels: {
      enabled: true
  },
       fillOpacity: 0.5
   }
},
      series: [
         {
            name: 'Water Usage',
            data: this.get_flat_chart_report.value_list
         }
        
      ]
   };
  }



  ngOnInit() {
   this.getConsumerlists();
   this.getFlatServiceDetails();
   
  }

}
