import { Injectable } from '@angular/core';
import {HttpClient,HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import { globalToken } from './globaltoken';

@Injectable({
  providedIn: 'root'
})
export class AuthorizepageaccessService {

  constructor(private http:HttpClient) { }
   
  urls = new globalToken().globalUrl;
   httpoptions = {
    headers:new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': this.urls.token
    })
   }


  getAuthrizationListForpageLock():Observable<any>{
    return this.http.get(this.urls.urlLink+'/get_authorized_page_by_user', this.httpoptions)
  }

}
