import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { CityService } from './city.service';

@Component({
  selector: 'app-city',
  templateUrl: './city.component.html',
  styleUrls: ['./city.component.css']
})
export class CityComponent implements OnInit {

  constructor(private fb: FormBuilder, private cityService: CityService) { }

  getCityDetailList: any;
  getCityLists() {
    this.cityService.getCityDetails().subscribe(response => {
      this.getCityDetailList = response.city_list;
    });
  }

  get_create_status;
  createCityForms = this.fb.group({
    city_name: ['']
  })

  createCities() {
    console.log(this.createCityForms.value)
    this.cityService.createCitiess(this.createCityForms.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Base Station')
        this.getCityLists();

      }
     
    })
  };

  get_delete_status;
  delete_json;
  deleteCities(cityObj) {
    this.delete_json = {
      '_id':cityObj._id
    }
    this.cityService.deleteCity(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getCityLists();
      }
     
    })
  }

  editResponse;
  editCityFunction(cityObj){
    let json_obj:any ={
      'city_name':cityObj.city_name,
      '_id':cityObj._id
    };
    this.cityService.editCity(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getCityLists();
   }
    });
  }

  ngOnInit() {
    this.getCityLists();
  }

}
