import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { globalToken } from '../../globaltoken';

@Injectable({
  providedIn: 'root'
})
export class InlettypeService {
  constructor(private http: HttpClient) { }

  urls = new globalToken().globalUrl;
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': this.urls.token
    })
  };

  getInletTypess(): Observable<any> {
    return this.http.get(this.urls.urlLink+'/metadata/GetAllInletTypes', this.httpOptions)
  }

  createInletTypess(inletType_obj:any): Observable<any> {
    return this.http.post(this.urls.urlLink+'/metadata/CreateInletType',inletType_obj, this.httpOptions)
  }
}
