import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { FlatService } from './flat.service';
import { FlattypeService } from '../flattype/flattype.service';
import { BlockService } from '../block/block.service';

@Component({
  selector: 'app-flat',
  templateUrl: './flat.component.html',
  styleUrls: ['./flat.component.css']
})
export class FlatComponent implements OnInit {

  constructor(private fb: FormBuilder, private flatservice: FlatService, private flattypeservice: FlattypeService, private blockservice: BlockService) { }

  getallFlatdetails;
  getallFlatData() {
    this.flatservice.getallFlatDetailss().subscribe(response => {
      this.getallFlatdetails = response.flat_details;
    });

  }

  getBlockServiceDet;
  getBlockServiceDetailss() {
    this.blockservice.getBlockServiceDetails().subscribe(response => {
      this.getBlockServiceDet = response.block_details;
    });

  }

  getFlatTypeList: any;
  getFlatType() {
    this.flattypeservice.getFlatTypeDetails().subscribe(response => {
      this.getFlatTypeList = response.flat_types_list;
    });

  }

  get_create_status;
  createFlatForm = this.fb.group({
    block_id: [''],
    flat_name: [''],
    flat_type: ['']
  })


  createFlats(created_flats) {
    this.flatservice.createFlatss(created_flats).subscribe(response => {
      this.get_create_status = response.response;
      if (response.status == 200) {
        alert('Sucessfully Added Flats')
        this.getallFlatData();
      }

    })
  };

  get_delete_status;
  delete_json;
  deleteFlats(flatObj) {
    this.delete_json = {
      '_id':flatObj._id
    }
    this.flatservice.deleteFlats(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getallFlatData();
      }
     
    })
  }


  editResponse;
  editFlatsFunction(flatDetailObj){
    let json_obj:any ={
      'block_id':flatDetailObj.block_id,
      'flat_name':flatDetailObj.flat_name,
      'flat_type':flatDetailObj.flat_type,
      '_id':flatDetailObj._id
    };
    this.flatservice.editFlats(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getallFlatData();
   }
    });
  }


  ngOnInit() {
    this.getallFlatData();
    this.getFlatType();
    this.getBlockServiceDetailss();
  }

}
