import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import {BuildingService} from './building.service';
import { BuildingtypeService } from '../buildingtype/buildingtype.service';
import { PipetypeService } from '../pipetype/pipetype.service';
import { CityService } from '../city/city.service';

@Component({
  selector: 'app-building',
  templateUrl: './building.component.html',
  styleUrls: ['./building.component.css']
})
export class BuildingComponent implements OnInit {

  constructor(private fb: FormBuilder, private buildingService: BuildingService, private buildingtypeService: BuildingtypeService, private pipetypeservice: PipetypeService,
    private cityService: CityService) { }

  getBuildingDet;
  getBuildingDetailss(){
    this.buildingService.getBuildingDetails().subscribe(response => {
     this.getBuildingDet = response.building_details;
 }); 
}

getCityDetailList: any;
getCityLists() {
  this.cityService.getCityDetails().subscribe(response => {
    this.getCityDetailList = response.city_list;
  });
}

getPipeData;
getPipeDetailsList() {
  this.pipetypeservice.getPipeDetailss().subscribe(response => {
    this.getPipeData = response.pipe_type_list;
  });
}

getBuildingTypeList: any;
  getBuildingTypeDet() {
    this.buildingtypeService.getBuildingTypeDetails().subscribe(response => {
      this.getBuildingTypeList = response.building_types;
    });

  }

 get_create_status;
 createBuildingsForms = this.fb.group({
  building_id: [''],
  building_name: [''],
  building_type: [''],
  logo: [''],
  pipe_type:[''],
  pipe_size_horizontal: [''],
  pipe_size_vertical: [''],
  max_floors: [''],
  num_of_blocks: [''],
  no_of_OHT:[''],
  no_of_ducts: [''],
  ip_address:[''],
  service_provider: [''],
  no_of_bhk: [''],
  no_of_other_types: [''],
  muncipal_water: [''],
  no_of_bores:[''],
  no_of_sumps: [''],
  consumption_without_metering:[''],
  monthly_water_expense: [''],
  STP_reuse: [''],
  website: [''],
  city_id: [''],
  address:['']
  })


  
  createBuildings() {
    console.log(this.createBuildingsForms.value)
    this.buildingService.createBuilding(this.createBuildingsForms.value).subscribe(response => {
      this.get_create_status = response;
      if(response.status == 200){
        alert('Sucessfully Added Base Station')
        this.getBuildingDetailss();

      }
     
    })
  }

  get_delete_status;
  delete_json;
  deleteBuildings(baseBuildingObj) {
    this.delete_json = {
      '_id':baseBuildingObj._id
    }
    this.buildingService.deleteBuilding(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getBuildingDetailss();
      }
     
    })
  }

  editResponse;
  editEachBuildingFunction(buildingdetailObj){
    let json_obj:any ={
      'building_id':buildingdetailObj.building_id,
      'building_name':buildingdetailObj.building_name,
      'building_type':buildingdetailObj.building_type,
      'pipe_type':buildingdetailObj.pipe_type,
      'pipe_size_horizontal':buildingdetailObj.pipe_size_horizontal,
      'pipe_size_vertical':buildingdetailObj.pipe_size_vertical,
      'max_floors':buildingdetailObj.max_floors,
      'num_of_blocks':buildingdetailObj.num_of_blocks,
      'no_of_OHT':buildingdetailObj.no_of_OHT,
      'no_of_ducts':buildingdetailObj.no_of_ducts,
      'ip_address':buildingdetailObj.ip_address,
      'service_provider':buildingdetailObj.service_provider,
      'no_of_bhk':buildingdetailObj.no_of_bhk,
      'no_of_other_types':buildingdetailObj.no_of_other_types,
      'muncipal_water':buildingdetailObj.muncipal_water,
      'no_of_bores':buildingdetailObj.no_of_bores,
      'no_of_sumps':buildingdetailObj.no_of_sumps,
      'consumption_without_metering':buildingdetailObj.consumption_without_metering,
      'monthly_water_expense':buildingdetailObj.monthly_water_expense,
      'STP_reuse':buildingdetailObj.STP_reuse,
      'website':buildingdetailObj.website,
      'city_id':buildingdetailObj.city._id,
      'address':buildingdetailObj.address,
      'latitude':buildingdetailObj.latitude,
      'longitude':buildingdetailObj.longitude,
      '_id':buildingdetailObj._id
    };
    this.buildingService.editBuilding(json_obj).subscribe((res:any)=>{
   this.editResponse = res;
   if(res.response =='success'){
    this.getBuildingDetailss();
   }
    });
  }

    
  

  ngOnInit() {
    this.getBuildingDetailss();
    this.getBuildingTypeDet();
    this.getPipeDetailsList();
    this.getCityLists();
  }

}
