import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { globalToken } from '../../globaltoken';

@Injectable({
  providedIn: 'root'
})
export class PipetypeService {

  constructor(private http: HttpClient) { }

  urls = new globalToken().globalUrl;
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': this.urls.token
    })
  };

  getPipeDetailss(): Observable<any> {
    return this.http.get(this.urls.urlLink+'/metadata/GetAllPipeTypes', this.httpOptions)
  }

  createPipeTypess(createPipetypeForm:any): Observable<any> {
    return this.http.post(this.urls.urlLink+'/metadata/CreatePipeType',createPipetypeForm, this.httpOptions)
  }
}
