import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { BuildingtypeService } from './buildingtype.service';

@Component({
  selector: 'app-buildingtype',
  templateUrl: './buildingtype.component.html',
  styleUrls: ['./buildingtype.component.css']
})
export class BuildingtypeComponent implements OnInit {

  constructor(private fb: FormBuilder, private buildingtypeService: BuildingtypeService) { }

  getBuildingTypeList: any;
  getBuildingTypeDet() {
    this.buildingtypeService.getBuildingTypeDetails().subscribe(response => {
      this.getBuildingTypeList = response.building_types;
    });

  }

  get_create_status;
  createBuildingTypeForm = this.fb.group({
    building_type: ['']
  })


  
  createBuildingTypes() {
    console.log(this.createBuildingTypeForm.value)
    this.buildingtypeService.createBuildingTypeList(this.createBuildingTypeForm.value).subscribe(response => {
      this.get_create_status = response.response;
      if(response.status == 200){
        alert('Sucessfully Added Base Station')
        this.getBuildingTypeDet();

      }
     
    })
  };

  get_delete_status;
  delete_json;
  deleteBuildingType(buildingTypeObj) {
    this.delete_json = {
      'building_type':buildingTypeObj
    }
    this.buildingtypeService.buildingTypes(this.delete_json).subscribe(response => {
      this.get_delete_status = response.response;
      if(response.status == 200){
        this.getBuildingTypeDet();
      }
     
    })
  }


  ngOnInit() {
    this.getBuildingTypeDet();
  }

}
