import { Component, OnInit } from '@angular/core';
import {TargetByFlatService} from './target-by-flat.service';
import {FlatService} from '../../metadata/flat/flat.service';
import {FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-target-by-flat',
  templateUrl: './target-by-flat.component.html',
  styleUrls: ['./target-by-flat.component.css']
})
export class TargetByFlatComponent implements OnInit {

  constructor(private targetbyFlatServi:TargetByFlatService, private flatServi:FlatService, private fb:FormBuilder) { }

  getFlatList;
  getFlatListFunction(){
    this.flatServi.getallFlatDetailss().subscribe((res:any)=>{
      this.getFlatList = res.flat_details;
    })
  }


  getlistofFlattarget:any;
  getlistofFlatTargetedFunction(){
    this.targetbyFlatServi.getlistofallFlatTargetService().subscribe((res:any)=>{
      this.getlistofFlattarget = res.target_list;
    })
  }

  createTargetForm = this.fb.group({
    flat_id: [''],
    target_value: ['']
  })

  setTargetResponse:any;
  addedResponseMsg:any;
  setTargetByFlat(){
    const json_obj = {
      'flat_id':this.createTargetForm.value.flat_id,
      'target_value':this.createTargetForm.value.target_value 
    };
    this.targetbyFlatServi.setTargetByFlatService(json_obj).subscribe((res:any)=>{
      this.setTargetResponse = res;
      this.addedResponseMsg = true;
      if(res.response == 'success'){
        this.getlistofFlatTargetedFunction();
      }
      setTimeout(() => {
        this.addedResponseMsg = false;
      },3000);
    });
  }


 
  eachlistofBuildingtarget:any;
  loopTable:any;
  selectBuilding:any;
  getTargetbuFlatIdFunction(FlatId){
    const jsonLst = {
      'flat_id':FlatId
    };
    this.loopTable =true; 
    if(FlatId){
      this.targetbyFlatServi.getTargetByFlatidService(jsonLst).subscribe((res:any)=>{
        this.eachlistofBuildingtarget = res.response;
      })
    }
    else {
      this.getlistofFlatTargetedFunction();
    }
  
  }

  editTargetbyBuildingFunction(targetObj,flag){
    const jsonObj = {
      'flat_id':targetObj.flat_id,
      'target_value':targetObj.target_value
    }
    this.targetbyFlatServi.setTargetByFlatService(jsonObj).subscribe((res:any)=>{
      this.eachlistofBuildingtarget = res.response;
      if(res.response == 'success'){
        if(flag){
          this.getlistofFlatTargetedFunction();
        }
        else {
        this.getTargetbuFlatIdFunction(this.selectBuilding);
        }
        
      }
    })

  }
  
  ngOnInit() {
    this.getFlatListFunction();
    this.getlistofFlatTargetedFunction();
  }

}
