import { Component, OnInit } from '@angular/core';
import { SignupService } from './signup.service';
import { FormBuilder } from '@angular/forms';
import {  Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  constructor(private signupServi:SignupService, private fb: FormBuilder, public router: Router) { }

  signup_token;
  signupForm = this.fb.group({
    email:[''],
    password:['']
  })

  loader;
  wrong_password;
  signup(){
  this.loader = true;
   this.signupServi.signupintoserver(this.signupForm.value).subscribe(response => {
    this.signup_token = response;
    this.loader = false;
    if(this.signup_token.token != 'Error Token'){ 
     if(this.signup_token.token != 'Incorrect Password'){
      this.router.navigate(['']);
    }
    else {
      this.wrong_password = true;
      setTimeout(()=>{
        this.wrong_password = false;
      },3000)
    }
  }
    else {
      this.wrong_password = true;
      setTimeout(()=>{
        this.wrong_password = false;
      },3000)
    }
   
}); 
  };

  ngOnInit() {
  }

}
