import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import {HomeServiceService} from './home-service.service';
import {BuildingService} from '../metadata/building/building.service';
import { FormBuilder } from '@angular/forms';
import Bullet from 'highcharts/modules/bullet';
Bullet(Highcharts);

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {
 
  constructor(private homeserviceService: HomeServiceService, private buildingservice:BuildingService, private fb: FormBuilder) { }
  

  selectedView:any = 'month';


  RTUreportResponse;
  loader1;
  getBuildingList;
  getBuildingdata() {
    this.buildingservice.getBuildingDetails().subscribe(response => {
      this.getBuildingList = response.building_details;
      this.selectedOption = response.building_details[0]._id;
      setTimeout(()=>{
        this.get_block_building_report();
      },400);
    });
  }

  
  years:string[] = ["2019","2020","2021","2022","2023","2024","2025"];
  selectedYear = "2020";

   monthList:any[] = [
    {'monthname':'Jan', 'monthnumber':1},
    {'monthname':'Feb', 'monthnumber':2},
    {'monthname':'Mar', 'monthnumber':3},
    {'monthname':'Apr', 'monthnumber':4},
    {'monthname':'May', 'monthnumber':5},
    {'monthname':'Jun', 'monthnumber':6},
    {'monthname':'Jul', 'monthnumber':7},
    {'monthname':'Aug', 'monthnumber':8},
    {'monthname':'Sep', 'monthnumber':9},
    {'monthname':'Oct', 'monthnumber':11},
    {'monthname':'Nov', 'monthnumber':12},
  ];
  

  building_id:any;
  selectedOption:any
  get_rtu_id(selectedOption){
    this.building_id = selectedOption;
  };

  selected_year;
  get_year(selectedYear){
    this.selected_year = selectedYear;    
  };
  selectedMonth:any=1;
  selected_month:any = 1;
  get_Month(selectedM){
    this.selected_month = Number(selectedM);
    
  }

  
  get_building_report_day(){

    this.loader1 = true;
    const json_obj = {
      'building_id':(!this.building_id?this.selectedOption:this.building_id),
      'year':(!this.selected_year?this.selectedYear:this.selected_year),
      'month':this.selected_month
    };
    this.homeserviceService.getBuildingconsumptionDayDetails(json_obj).subscribe(res=>{
          this.RTUreportResponse= res.response;
          this.bulletChart(this.RTUreportResponse);
        });
  }


  get_building_report_monthly(){
    this.loader1 = true;
    const json_obj = {
      'building_id':(!this.building_id?this.selectedOption:this.building_id),
      'year':Number(!this.selected_year?this.selectedYear:this.selected_year),
      'month':this.selected_month
    };
    this.homeserviceService.getBuildingconsumptionMonthlyDetails(json_obj).subscribe(res=>{
          this.RTUreportResponse= res.response;
          this.bulletChart(this.RTUreportResponse);
        });
  }


  get_building_report_year(){
    this.loader1 = true;
    const json_obj = {
      'building_id':(!this.building_id?this.selectedOption:this.building_id)
    };
    this.homeserviceService.getBuildingconsumptionYearlyDetails(json_obj).subscribe(res=>{
          this.RTUreportResponse= res.response;
          this.bulletChart(this.RTUreportResponse);
        });
  }


// call all month year and day api based on function
get_block_building_report(){
  if(this.selectedView == 'year'){
    this.get_building_report_monthly();
  } else if(this.selectedView == 'month'){
    this.get_building_report_monthly();
  }
  else if(this.selectedView == 'day'){
    this.get_building_report_monthly();
  }
};

// call all month year and day api based on function
  

  highcharts;
  chartOptions;
  bulletChart(bulletData) {
    this.loader1 = false;
   this.highcharts = Highcharts;
  this.chartOptions = {   
    chart: {
      marginTop: 40,
       inverted: true,
      marginLeft: 60,
      type: 'bullet'
  },
    legend: {
      enabled: false
  },
  title: {
      text: 'Current Consumption ' +(!this.selected_year?'':this.selected_year)
  },
  xAxis: {
      categories: ['Usage']
  },
   exporting: {
      enabled: false
  },
  yAxis: {
   gridLineWidth: 0,
      plotBands: [{
          from: 0,
          to: (!bulletData?30000:(bulletData.target==0)?30000:(bulletData.target + 80000)),
          color: '#91ddff'
      }],
      title: null
  },
   plotOptions: {
      series: {
          pointPadding: 0.25,
          borderWidth: 0,
          color: (!bulletData?'#8BC34A': (bulletData.percentage==0)?'#8BC34A': (bulletData.percentage < 70)?'#8BC34A':((bulletData.percentage > 70) && (bulletData.percentage < 90))?'#FF2AA0':'#FF2A1F'),
          targetOptions: {
              width: '200%'
          }
      }
  },
  series: [{
      data: [{
          y: (!bulletData?0:(bulletData.value==0)?100 : (bulletData.value)),
          target: (!bulletData?25000:(bulletData.target==0)?25000:bulletData.target)
      }]
  }],
  tooltip: {
      pointFormat: '<b>{point.y}</b> (with target at {point.target})'
  }
  };
 }

  ngOnInit() {
  //  this.getRTUConsumptionMonthly();
  this.getBuildingdata();  
  }
}


