import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoginModel } from './login.model';
import { globalToken } from '../globaltoken';
// import { URL } from '../shared/sharedconstants';



@Injectable({ providedIn: 'root' })
export class LoginService {
    constructor(private http: HttpClient) {}
   

    urls = new globalToken().globalUrl;
    httpOptions = {
        headers: new HttpHeaders({ 
            'Content-Type': 'application/x-www-form-urlencoded'

     })
      };

    
    logintoserver(email: string, password: string):Observable<any> {
        const login_model = new LoginModel(email, password);
        return this.http.post(this.urls.urlLink+'/get_token_from_email_and_password', login_model)
           
    }
    
} 