import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import { BuildingReportService } from './building-report.service';
import { BuildingService } from '../../metadata/building/building.service'

@Component({
  selector: 'app-buildingreport',
 // moduleId: module.id,
  templateUrl: './buildingreport.component.html',
  styleUrls: ['./buildingreport.component.css']
})
export class BuildingreportComponent implements OnInit {

  constructor(private buildingService:BuildingService, private buildingReportService:BuildingReportService) { }

  get_building_table_report;
  get_building_chart_report


  years:string[] = ["2019","2020","2021","2022","2023","2024","2025"];
  selectedYear:any = "2020";

   monthList:any[] = [
    {'monthname':'Jan', 'monthnumber':1},
    {'monthname':'Feb', 'monthnumber':2},
    {'monthname':'Mar', 'monthnumber':3},
    {'monthname':'Apr', 'monthnumber':4},
    {'monthname':'May', 'monthnumber':5},
    {'monthname':'Jun', 'monthnumber':6},
    {'monthname':'Jul', 'monthnumber':7},
    {'monthname':'Aug', 'monthnumber':8},
    {'monthname':'Sep', 'monthnumber':9},
    {'monthname':'Oct', 'monthnumber':11},
    {'monthname':'Nov', 'monthnumber':12},
  ];

  
  get_building_list;
  getBuildingServiceDetails() {
    this.buildingService.getBuildingDetails().subscribe(response => {
      this.get_building_list = response.building_details;
      this.selectedOption = response.building_details[0]._id;
      if(response.status == '200'){
         this.get_buildingreport_for_current_month();
      }
    })
  };


  building_ids;
  selectedOption:any;
  get_buildingReport(selectedOption){
   this.building_ids = selectedOption;
  };



  get_buildingreport_for_current_month() {
   let sendParam = {
      'building_id':(!this.building_ids?this.selectedOption:this.building_ids),
      'year':(!this.selected_year?this.selectedYear:Number(this.selected_year))
   };

  //  month view table report
   this.buildingReportService.getBuildingReportForCurrentMonth(sendParam).subscribe(response => {
     this.get_building_table_report = response.response;
   });

  //  month view chart report
  this.buildingReportService.getBuildingCosumptionChartForMonth(sendParam).subscribe(response => {
     this.get_building_chart_report = response.response;
     if(response.response){
        this.linechart();
     }
   });

 };


 get_buildingreport_for_current_day() {
   let sendParam = {
      'building_id':(!this.building_ids?this.selectedOption:this.building_ids),
      'month':(!this.selected_month?this.selectedMonth:Number(this.selected_month)),
      'year':(!this.selected_year?this.selectedYear: Number(this.selected_year))
   };

  //  day view table
   this.buildingReportService.getBuildingReportForCurrentDay(sendParam).subscribe(response => {
     this.get_building_table_report = response.response;
   })

  //  day view chart
  this.buildingReportService.getBuildingCosumptionChartForDay(sendParam).subscribe(response => {
     this.get_building_chart_report = response.response;
     if(response.response){
      this.linechart();
   }
   })
 };


 get_buildingreport_for_current_year() {
   let sendParam = {
      'building_id':(!this.building_ids?this.selectedOption:this.building_ids)
   };
  //  year view table
   this.buildingReportService.getBuildingReportForCurrentYear(sendParam).subscribe(response => {
     this.get_building_table_report = response.response;
   })

  //  year view chart
   this.buildingReportService.getBuildingCosumptionChartForYear(sendParam).subscribe(response => {
     this.get_building_chart_report = response.response;
     if(response.response){
      this.linechart();
   }
   })
 };




 get_buildingreport_for_search_date() {
   let sendParam = {
      'building_id':(!this.building_ids?this.selectedOption:this.building_ids)
   };

   this.buildingReportService.getBuildingReportForSearchDate(sendParam).subscribe(response => {
     this.get_building_table_report = response.response;
   })
 };


 selected_year:any;
 get_year(selectedYear){
    this.selected_year = selectedYear;

 }

 selected_month;
 selectedMonth:any = '1';
 get_month(selectedMonth){
  this.selected_month = selectedMonth;

 }

 selectedView:any= 'month';
 get_building_report(){
   let param = this.selectedView;
   switch(param){
      case 'year':
         this.get_buildingreport_for_current_year();
      break;
      case 'month':
      this.get_buildingreport_for_current_month();
      break;
      case 'day':
         this.get_buildingreport_for_current_day();
      break;
   }
 }

  highchartss;
  chartOptions;

  linechart(){
    this.highchartss = Highcharts;
   this.chartOptions = {   
      chart: {
         type: "spline"
      },
      title: {
         text: "Monthly Average Usage"
      },
      subtitle: {
         text: "Detail Report"
      },
      xAxis:{
         categories:this.get_building_chart_report.header_list
      },
      yAxis: {          
         title:{
            text:"Monthly Report"
         } 
      },
      tooltip: {
         valueSuffix:" °C"
      },
      plotOptions: {
         spline: {
            dataLabels: {
               enabled: true
           },
             fillOpacity: 0.5
         }
      },
      series: [
         {
            name: 'Water Usage',
            data: this.get_building_chart_report.value_list
         }
        
      ]
   };
  }


  ngOnInit() {
   this.getBuildingServiceDetails();
  }

}
