import { Component, OnInit } from '@angular/core';
import {TargetByBuildingService} from './target-by-building.service';
import {BuildingService} from '../../metadata/building/building.service';
import {FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-target-by-building',
  templateUrl: './target-by-building.component.html',
  styleUrls: ['./target-by-building.component.css']
})
export class TargetByBuildingComponent implements OnInit {

  constructor(private targetbyBuildingServi:TargetByBuildingService, private buildingServi:BuildingService, private fb:FormBuilder) { }
 
  getBuildingList;
  getBuildingListFunction(){
    this.buildingServi.getBuildingDetails().subscribe((res:any)=>{
      this.getBuildingList = res.building_details;
    })
  }


  getlistofBuildingtarget:any;
  getlistofBuildingTargetedFunction(){
    this.targetbyBuildingServi.getlistofallBuildingTargetService().subscribe((res:any)=>{
      this.getlistofBuildingtarget = res.target_list;
    })
  }

  createTargetForm = this.fb.group({
    building_id: [''],
    target_value: ['']
  })

  setTargetResponse:any;
  addedResponseMsg:any;
  setTargetByBuilding(){
    const json_obj = {
      'building_id':this.createTargetForm.value.building_id,
      'target_value':this.createTargetForm.value.target_value 
    };
    this.targetbyBuildingServi.setTargetByBuildingService(json_obj).subscribe((res:any)=>{
      this.setTargetResponse = res;
      this.addedResponseMsg = true;
      if(res.response == 'success'){
        this.getlistofBuildingTargetedFunction();
      }
      setTimeout(() => {
        this.addedResponseMsg = false;
      },3000);
    });
  }


 
  eachlistofBuildingtarget:any;
  loopTable:any;
  selectBuilding:any;
  getTargetbuBuildingIdFunction(buildingId){
    const jsonLst = {
      'building_id':buildingId
    };
    this.loopTable =true; 
    if(buildingId){
      this.targetbyBuildingServi.getTargetByBuildingidService(jsonLst).subscribe((res:any)=>{
        this.eachlistofBuildingtarget = res.response;
      })
    }
    else {
      this.getlistofBuildingTargetedFunction();
    }
  
  }

  editTargetbyBuildingFunction(targetObj,flag){
    const jsonObj = {
      'building_id':targetObj.building_id,
      'target_value':targetObj.target_value
    }
    this.targetbyBuildingServi.setTargetByBuildingService(jsonObj).subscribe((res:any)=>{
      this.eachlistofBuildingtarget = res.response;
      if(res.response == 'success'){
        if(flag){
          this.getlistofBuildingTargetedFunction();
        }
        else {
        this.getTargetbuBuildingIdFunction(this.selectBuilding);
        }
        
      }
    })

  }
  
  ngOnInit() {
    this.getBuildingListFunction();
    this.getlistofBuildingTargetedFunction();
  }

}
