import { Component, OnInit } from '@angular/core';
import {TargetService} from './target.service';
import {BlockService} from '../metadata/block/block.service';
import {BuildingService} from '../metadata/building/building.service';
import {FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-target',
  templateUrl: './target.component.html',
  styleUrls: ['./target.component.css']
})
export class TargetComponent implements OnInit {

  constructor(private targetService:TargetService, private blockService:BlockService, private fb: FormBuilder, private buildingService:BuildingService) { }
  
  
  loader1=true;

  building_list;

  getBuildingList(){
    this.buildingService.getBuildingDetails().subscribe(res=>{
      this.building_list = res.building_details;
      this.selectBuilding = res.building_details[0]._id;
      this.selected_building_id = res.building_details[0]._id;
      this.getlistByBuildingid();
      this.loader1=false;
    });
  };

  selected_building_id;
  selectBuilding:any;
  select_building(building_id){
    this.selected_building_id = building_id;
    this.getlistByBuildingid();
  };


  blockList;
  getBlockList(){
    this.blockService.getBlockServiceDetails().subscribe(res=>{
     this.blockList = res.block_details;
    });
  };

  block_id;
  select_block(selected_block_id){
    this.block_id = selected_block_id;
    this.getTargetListByBlockID();
  };


  createTargetForm = this.fb.group({
    block_id: [''],
    target: ['']
  })


  setTargetResponse;
  setTargetByBLOCKID(){
    const json_obj = {
      'block_id':this.createTargetForm.value.block_id,
      'target_value':this.createTargetForm.value.target 
    };
    this.targetService.setTargetByBlockId(json_obj).subscribe(res=>{
      this.setTargetResponse = res;
    });
  };
 
  get_targetlist_for_table;
  getTargetListByBlockID(){
  
    const json_obj={
      'block_id':this.block_id
    };
    this.targetService.getTargetByBlock_id(json_obj).subscribe(res=>{
      this.get_targetlist_for_table = res.response;
      
    });
  };


  getlistByBuildingid(){
    const json_obj = {
      'building_id':this.selected_building_id
    };
    this.targetService.getlistTargetByBuildingId(json_obj).subscribe(res=>{
      this.get_targetlist_for_table = res.target_list;
    });
  }




  ngOnInit() {
    this.getBlockList();
    this.getBuildingList();
  }
  

}
