import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import {FlatReportService} from './flat-report.service';
import {FlatService} from '../../metadata/flat/flat.service';

@Component({
  selector: 'app-flatreport',
  //moduleId: module.id,
  templateUrl: './flatreport.component.html',
  styleUrls: ['./flatreport.component.css']
})
export class FlatreportComponent implements OnInit {

  constructor(private flatService:FlatService, private flatReportService:FlatReportService) { }

  get_block_table_report;
  get_flat_chart_report;
  get_flat_table_report;
  selectedView:any='month';

  get_flat_list;
  getFlatServiceDetails() {
    this.flatService.getallFlatDetailss().subscribe(response => {
      this.get_flat_list = response.flat_details;
      if(response.status == '200'){
         this.selectedOption = response.flat_details[0]._id;
         this.get_flatreport_for_current_month();
      }
    
    })
  }

  monthList:any[] = [
   {'monthname':'Jan', 'monthnumber':1},
   {'monthname':'Feb', 'monthnumber':2},
   {'monthname':'Mar', 'monthnumber':3},
   {'monthname':'Apr', 'monthnumber':4},
   {'monthname':'May', 'monthnumber':5},
   {'monthname':'Jun', 'monthnumber':6},
   {'monthname':'Jul', 'monthnumber':7},
   {'monthname':'Aug', 'monthnumber':8},
   {'monthname':'Sep', 'monthnumber':9},
   {'monthname':'Oct', 'monthnumber':11},
   {'monthname':'Nov', 'monthnumber':12},
 ];

  selected_year:any;
  selectedYear:any = '2020';
  get_year(selectedYear){
     this.selected_year = selectedYear;
  }
 
  selected_month;
  selectedMonth:any;
  get_month(selectedMonth){
   this.selected_month = selectedMonth; 
  }

  flat_ids;
  selectedOption:any;
  get_flatReport(selectedOption){
   this.flat_ids = selectedOption;
  }


  get_flatreport_for_current_month() {
   let sendParam = {
      'flat_id':(!this.flat_ids?this.selectedOption:this.flat_ids),
      'year':Number(!this.selected_year?this.selectedYear:this.selected_year)
   };

  //  month view chart report
  this.flatReportService.getFlatCosumptionChartForMonth(sendParam).subscribe(response => {
     this.get_flat_chart_report = response.response;
     if(response.response){
      this.linechart();
     }
   });
   // table
   this.flatReportService.getFlatConsumptionTableApi(sendParam).subscribe(response => {
      this.get_flat_table_report = response;
    
    });

 }


 get_flatreport_for_current_day() {
  
   let sendParam = {
      'flat_id':(!this.flat_ids?this.selectedOption:this.flat_ids),
      'month':Number(!this.selected_month?1:this.selected_month),
      'year':(!this.selected_year?2019: Number(this.selected_year))
   };

   this.selectedMonth = sendParam.month;
  //  day view chart
  this.flatReportService.getFlatCosumptionChartForDay(sendParam).subscribe(response => {
     this.get_flat_chart_report = response.response;
     if(response.response){
      this.linechart();
     }
   })

     // table
     this.flatReportService.getFlatConsumptionTableApi(sendParam).subscribe(response => {
      this.get_flat_table_report = response;
    
    });
 }


 get_flatreport_for_current_year() {
   
   let sendParam = {
      'flat_id':(!this.flat_ids?this.selectedOption:this.flat_ids)
   };

  //  year view chart
   this.flatReportService.getFlatCosumptionChartForYear(sendParam).subscribe(response => {
     this.get_flat_chart_report = response.response;
     if(response.response){
      this.linechart();
     }
   })
     // table
     this.flatReportService.getFlatConsumptionTableApi(sendParam).subscribe(response => {
      this.get_flat_table_report = response;
    
    });
 }

 get_flat_report(){
    let param = this.selectedView;
   switch(param){
      case 'year':
         this.get_flatreport_for_current_year();
      break;
      case 'month':
      this.get_flatreport_for_current_month();
      break;
      case 'day':
         this.get_flatreport_for_current_day();
      break;
   }
 }





  highcharts;
  chartOptions;

  linechart(){
    this.highcharts = Highcharts;
   this.chartOptions = {   
      chart: {
         type: 'areaspline'
      },
      title: {
         text: "Monthly Average Usage"
      },
      subtitle: {
         text: "Detail Report"
      },
      xAxis:{
         categories:this.get_flat_chart_report.header_list
      },
      yAxis: {          
         title:{
            text:"Monthly Report"
         } 
      },
      legend: {
         layout: 'vertical',
         align: 'left',
         verticalAlign: 'top',
         x: 150,
         y: 100,
         floating: true,
         borderWidth: 1,
         backgroundColor:
             Highcharts.defaultOptions.legend.backgroundColor || '#FFFFFF'
     },
     tooltip: {
      shared: true,
      valueSuffix: ' units'
  },
  plotOptions: {
   areaspline: {
      dataLabels: {
         enabled: true
     },
       fillOpacity: 0.5
   }
},
      series: [
         {
            name: 'Water Usage',
            data: this.get_flat_chart_report.value_list
         }
        
      ]
   };
  }

  ngOnInit() {
    
    this.getFlatServiceDetails();
  }

}



   

   
  